import React, { useState, useEffect, Suspense, useLayoutEffect, useMemo } from 'react'
import useSetState from '@utils/js/useSetState'
import ReactDOM from 'react-dom'
export default function Demo1(props) {
    const [data, setData] = useState('yellow')
    const [state, setState] = useSetState({
        formData: {
            name: ''
        },
        formSubmitData: {}
    })
    // useLayoutEffect是发生在Effect之前
    useLayoutEffect(() => {
        // console.log('执行useLayoutEffect')
        setState({
            formData: Object.assign({}, {
                name: '1111111111111111111111',
                label: '2'
            })
        })
    }, [])
    // 使用useMemo来降低渲染粒度
    const getNum = useMemo(() => {
        return state.formData.name + '拼接一串'
    }, [state])
    const RenderDemo = useMemo(() => {
        // console.log('RenderDemo渲染了')
        return <div>{state.formData.name}</div>
    }, [state])
    const RenderElement = useMemo(() => {
        // console.log('renderElement渲染了')
        return <div>{data}</div>
    }, [data])
    console.log(RenderDemo)
    return (
        <>
            {
                RenderDemo
            }
            {
                RenderElement
            }
            {
                getNum
            }
            <br />
            <button onClick={() => setState({ formData: { name: 'red' } })}>red</button>
            <button onClick={() => setData('blue')}>blue</button>
            <button onClick={() => {
                setState({ formData: { name: 'blue' } })
                setData('red')
            }}>yellow</button>
        </>
    )
}